
.ColorFilter {
		display: inline-block;
		width:calc(100%/8);
		height:25px;
}

.HeaderPanel {
	z-index: 100;
	position: absolute;
	max-width: 200px;
}
.TitleGradient{
		position: absolute;
	padding: 5px;
	z-index: 100;
	height: 92px;
	width: 190px;
	opacity: 1;
}
.shade_Title{
	z-index: 101;
	position: absolute;
	top: 67px;
	height: 30px;
	width: 325px;
	opacity: .7;
	background: green;
	color: green;
	background-color: green;
}
.ToolTipGradient{
	position: absolute;
	top: 92px;
	padding: 5px;
	z-index: 50;
	height: 82px;
	width: 200px;
	opacity: .6;
}

.ToolTipShade{
	position: absolute;
	top: 62px;
	padding: 5px;
	z-index: 101;
	height: 115px;
	width: 200px;
	opacity: .80;

}
.logo_mobile{
		z-index: 150;
		position: absolute;
		padding: 5px;
		top: 0;
		height: 65px;
		width: 109px;
		max-height: 130px;
		max-width: 218px;
}
.panel {
	z-index: 250;
	position: absolute;
	top: 52px;
	left: 10px;
}
.ToolTipDescription{
	font-size: 12px;
	color: white;
	position: absolute;
	z-index: 101;
	line-height: 1;
	padding: 5px;
	left: 5px;
	top: 170px;

}
.ToolTipStatic {
		z-index: 101;
		font-size: 14px;
		color: rgba(0,47,91,1);
		position: absolute;
		line-height: .15;
		padding: 5px;
		top: 90px;
		left: 5px;
}
.ToolTipDynamic {
		line-height: 1.15;
		z-index: 101;
		position: absolute;
		font-size: 14px;
		color: white;
		text-align: left;
		top: 100px;
		left: 75px;

}
.ranked {
		position: absolute;
		top: 132px;
		left: 115px;
		z-index: 160;
		width: 30px;
}
.RankDown {
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 15px 7.5px 0 7.5px;
	border-color: red transparent transparent transparent;
}
.RankUp {
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 0px 7.5px 15px 7.5px;
	border-color: transparent transparent lightgreen transparent;
}
.panel h2
{
	font-size: 30px;
	color: rgba(0,47,91,1);
	line-height: 1;
	position: absolute;
	top:-10px;
	width:200px;

}
.footer
{
	display: flex;
	position: absolute;
	height: 100%;
	flex-direction:column-reverse;

	left: 10px;
	top: -2px;
	max-width: 70%;
}
.legendInfo{
	z-index: 100;
	display: flex;
	flex-direction:row;
	justify-content: space-between;
	font-size: 14px;
}

.legendInfo p{
	font-weight: bold;
	max-width: 40%;
	display: inline;
	line-height: 1.2;
	margin:  0;
	color: rgba(0,47,91,1);
}
.legend {
	z-index: 100;
	max-width: 700px;
	margin-bottom: -15px;
}
.waterMark_Desktop {
	z-index: 100;
	padding-top: 15px;
	padding-bottom: 2px;
	font-size: 12px;
	line-height: 0.5;
	font-style: oblique;
			color: rgba(0,47,91,1);
}
.waterMark_Mobile {
	z-index: 100;
	padding-top: 12px;
	padding-bottom: 3px;
	font-size: 8px;
	line-height: 0.4;
	font-style: oblique;
			color: rgba(0,47,91,1);
}
